import React, { Component } from 'react';
import './Introduction.css';
import aboutme from '../../images/aboutme.jpg';
import rap from '../../images/IMG_5520.GIF';
import {Link } from "react-router-dom";
export default class Introduction extends Component {

    constructor(props) {
        super(props);
        this.state={
            aboutme:[
                "还没毕业的老学长，就读于双非武工大，英语+软件工程,别问了，双专业，英语还行...",
                "软件工程专业,功课掌握一般，只是学过，简单算法,网络，数据结构问题可以参与",
                "你应该问我前端知识,自认为JS 基础不错，前台网站，后台系统，小程序开发都还可以",
                "框架？？？ 会vue,react,双向绑定,diff 算法欢迎讨论。"                
            ]
        }

    }


    render() {
        return (
            <div>
                <div className='abimg'><img src={aboutme} /></div>
                <div className='tellsomething' >
                    <p>万芷辉/hugo</p>
                    <h1>全栈开发工程师</h1>
                    <p>持续学习中……</p>
                </div>

                <div className='aboutme'>
                    <div className='left'>
                        <h1 className='lefttitle'>说说我自己</h1>
                        <img src={rap}></img>
                        <p >&nbsp;&nbsp; 时长一年的前端个人练习生 ，<br/>
                       喜欢唱、跳、rap、篮球……</p>
                    </div>
                    <div className='right'>
                        {this.state.aboutme.map(p=>{
                            return <p>{p}</p>
                        })}

                        <a href = 'https://github.com/hugo1o1' >废话不多说，直接看项目...</a>
                    </div>

                </div>
            </div>
        )
    }
}